<template>
  <section> 
    <div>
      <div class="col-lg-12 border p-2 float-left">
          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
              <div class="row cal_iconsbase" id="divHeader">
                  <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                      <div class="row label_group">
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('CALocation')}} :</b><span class="lighttext"> {{ this.AssetCalendarData[0].locatioN_NAME }}</span></div> </div>
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('CRM_PRODUCTS')}} : </b><span class="lighttext"> {{ this.AssetCalendarData[0].name }}</span></div> </div>
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('Total')}} : </b><span class="lighttext" > {{ this.AssetCalendarData[0].totaL_QUANTITY }}</span></div> </div>
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('UniqueName')}} : </b><span class="lighttext"> {{ this.AssetCalendarData[0].uniquE_NAME }}</span></div> </div>                        </div>
                        
                  </div>                  
              </div>
          </div>
          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0 mt-2">
              <div class="table-responsive">
                  <VCalendar
                      class="custom-calendar max-w-full"
                      :masks="masks"
                      :attributes="attributes"        
                      is-expanded                     

                     >                     
                      <template v-slot:day-content="{ attributes }">
                        <div class="flex flex-col h-full z-10 overflow-hidden" v-for="attr in attributes" :key="attr.key">                          
                          <div class="flex-grow overflow-y-auto overflow-x-auto d-block w-100" :class="attr.customData.class">                             
                            <p        
                              v-if="$options.filters.formatDate(attr.start) <= attr.end"                       
                              class="text-xs leading-tight rounded-sm p-1 mt-0 mb-1 text-primary"
                              >
                             
                            </p>
                            <span>{{ attr.customData.title }}</span>
                          </div>
                        </div>
                      </template>
                  </VCalendar>
              </div>
          </div>
      </div>
    </div> 
  </section>
</template>

<script>
import DataService from '../../service/DataService';
import VCalendar from 'v-calendar/lib/components/calendar.umd';
import 'v-calendar';

export default {
  components:{VCalendar},
  data() {  
    return {
      AssetCalendarData:[],
      masks: {
      weekdays: 'WWW',
    },
    attributes: [],      
  };
  },
  created:function(){        
      this.LoadAssetCalendar(); 
  },
  updated:{
    pages:function(){
      debugger
    },
  },
  methods:{

  
    LoadAssetCalendar:function(){ 
          var url=`userId=0&&locationId=${this.$route.params.lid}&assetCatId=${this.$route.params.aid}&rid=${this.$route.params.rid}&start=&end=&requestedEventType=`;
          DataService.LoadAssetCalendar(url).then(response=>{              
              this.AssetCalendarData = response.data;                
              this.AssetCalendarData.forEach((item,index)=>{
                console.log(item);               
                if(item.title == 'AVAILABLE'){                  
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                    // class: 'r-calendar-available text-white',
                      
                  },                 
                  highlight: 'green',
                  dates: [{ start: new Date(item.start), end: new Date(item.end) }],
                });                  
                }
                else if(item.title == 'RESERVED'){                    
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                   // class: 'r-calendar-reserved text-white',
                  },
                  highlight: 'Yellow',
                  dates:[{start: new Date(item.start), end: new Date(item.end)}]
                });
                }else if(item.title == 'ASSIGNED'){                    
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                    //class: 'r-calendar-assigned text-white',
                  },
                  highlight: 'Black',
                  dates:[{start: new Date(item.start), end: new Date(item.end)}]
                });
                } else if(item.title == 'IN-TRANSIT'){                    
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                    //class: 'r-calendar-intransit text-white',
                  },
                  highlight: 'Pink',
                  dates:[{start: new Date(item.start), end: new Date(item.end)}]
                });
                } else if(item.title == 'AUDIT (annual )'){                    
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                    //class: 'r-calendar-audit text-white',
                  },
                  highlight: 'blue',
                  dates:[{start: new Date(item.start), end: new Date(item.end)}]
                });
                } else if(item.title == 'CALIBRATION (quarterly )'){                    
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                    //class: 'r-calendar-grey text-white',
                  },
                  highlight:true,
                  dates:[{start: new Date(item.start), end: new Date(item.end)}]
                });
                } else {                    
                  this.attributes.push({
                  key:index+1,                    
                  customData:{
                    title: `${item.title} (${item.quantity})`,
                    //class: 'editable text-white',
                  },
                  highlight: 'purple',
                  dates:[{start: new Date(item.start), end: new Date(item.end)}]
                });
                }                                   
              });
          }); 
          console.log(this.attributes);         
      }
  }
};
</script>
<style >
  .custom-calendar.vc-container {--day-border: 1px solid #b8c2cc;--day-border-highlight: 1px solid #b8c2cc;--day-width: 90px;--day-height: 90px;--weekday-bg: #f8fafc;--weekday-border: 1px solid #eaeaea;border-radius: 0;width: 100%;}
  .vc-title[data-v-74ad501d] { font-size: 24px!important; font-weight: 400!important;}
  .vc-header {background-color: #d4d4d4; padding: 10px 18px 10px 18px!important;}
  .vc-weeks {padding: 0!important;}
  .vc-weekday { background-color: #e5e5e5;color:#5a5a5a!important; border-bottom:1px solid #c9c9c9;border-top: var(--weekday-border); padding: 10px 0!important; font-weight: 400 !important; font-size: 16px!important; line-height: 24px!important;}
  .vc-day {padding: 0 5px 3px 5px;text-align: left;height: var(--day-height); min-width: var(--day-width); background-color: #f7f7f7;border-right: 1px solid #dddddd; border-bottom: 1px solid #dddddd; min-height: 226px!important;}
  .day-label{font-size: 25px; font-weight: 400;}
  .weekday-1, .weekday-7 { background-color: #f7f7f7;}
  .on-bottom{ border-bottom: var(--day-border);}
  .weekday-1 { border-bottom: var(--day-border-highlight);}   
  .on-right{ border-right: var(--day-border);} 
  .vc-day-dots {margin-bottom: 5px;}  
  .r-calendar-available {background-color: #a6cdb2 !important;color: #fff !important;padding: 2px 5px!important;text-transform: capitalize;margin: 2px 0; font-size: 12px; font-weight: 500;}
  .r-calendar-audit {background-color: #a5c63b !important;color: #fff !important;padding: 2px !important;text-transform: capitalize;font-size: 12px;font-weight: 500; margin: 2px 0;}
  .r-calendar-grey {background-color: #737373 !important;color: #fff !important;padding: 2px !important;text-transform: capitalize;font-size: 12px;font-weight: 500;margin: 2px 0;}
  .vc-pane-container .vc-day-content{font-size: 30px !important;font-family: "OpenSansRegular", Arial; margin: 5px 0;display:flex;margin-top: 5px;margin-left: auto;}
</style>